body {
  margin: 0;
  font-family: 'Inter', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #E7F6FE;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
  cursor: pointer;
}

* {
  font-family: 'Inter', sans-serif;
}

li {
  list-style-type: none;
  margin-right: 30px;
}

ul {
  padding: 0;
}

.wrapper {
  // position: relative;
  background-color: #fff;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px;
  max-width: 1080px;
  margin: 50px auto;
}


header {
  display: flex;
  justify-content: space-between;
  padding: 40px;
  border-bottom: 2px solid #eaeaea;

  .headerLeft {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .headerInfo {
      p {
        opacity: .5;
      }
    }

    img {
      margin-right: 15px;
    }

    h3,
    p {
      margin: 0;
    }

    h3 {
      text-transform: uppercase;
    }
  }



  .headerRight {
    display: flex;
    justify-content: space-between;
    align-items: center;

    li {
      list-style-type: none;
      margin-right: 30px;

      .sumBuy {
        margin-left: 10px;
        font-weight: 700;
      }
    }
  }
}

.content {
  padding: 40px;

  h1 {
    margin: 0;
    // margin-bottom: 40px;
  }

  .cardContainer {
    display: flex;
    column-gap: 30px;

    .card {
      margin-top: 40px;
      border: 2px solid #f3f3f3;
      padding: 20px;
      width: 220px;
      border-radius: 40px;
      transition: all 0.2s ease-in-out;

      &:hover {
        box-shadow: 0px 14px 20px rgba(0, 0, 0, 0.05);
        transform: translateY(-5px);
      }

      .favorite {
        position: absolute;
        cursor: pointer;
      }

      h5 {
        font-weight: 400;
        font-size: 14px;
      }

      &Bottom {
        display: flex;
        justify-content: space-between;



        .addBtn {
          border: none;
          background-color: #FFFFFF;
          box-sizing: border-box;
          border-radius: 8px;
          width: 31px;
          height: 32px;
          padding: 0;
        }

      }

      &Info {
        display: flex;
        flex-direction: column;

        span {
          font-size: 13px;
          opacity: .5;
          text-transform: uppercase;
        }

        b {
          font-size: 13px;
        }
      }

    }
  }

  .sneakersRow {
    display: flex;
    column-gap: 30px;
  }

}

.search-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 40px;
}

.search-block {
  border: 1px solid #f2f2f2;
  border-radius: 10px;
  padding: 0 13px;
  // display: flex;

  input {
    border: none;
    padding: 10px;
    font-size: 16px;
    outline: none;
    width: 200px;
  }

}

.overlay {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.1);
  z-index: 1;
}

.drawer {
  position: fixed;
  width: 420px;
  right: 0;
  height: 95.3vh;
  background: #FFFFFF;
  padding: 30px;
  display: flex;
  flex-direction: column;
  // justify-content: space-between;

  h2 {
    margin: 0;
    margin-bottom: 30px;
    display: flex;
    justify-content: space-between;

    img {
      cursor: pointer;
    }
  }

  .items {
    margin-bottom: 40px;
    flex: 1;
    overflow: auto;

    &::-webkit-scrollbar {
      width: 10px;
      background-color: #f9f9fd;
    }

    &::-webkit-scrollbar-thumb {
      border-radius: 10px;
      background: linear-gradient(180deg, #c4c4c4, #b3b3b3);
    }
  }

  .cartItem {
    margin-bottom: 10px;
    border: 1px solid #f3f3f3;
    border-radius: 20px;
    overflow: hidden;
    display: flex;
    align-items: center;
    padding: 20px;

    .sneakersImg {
      margin-right: 20px;
    }

    .cartItemImg {
      height: 70px;
      width: 70px;
      background-size: contain;
      background-position: 0 2px;
      background-repeat: no-repeat;
      margin-right: 20px;
      flex: 1;
    }

    .sneakersAbout {
      margin-right: 50px;

      .sneakersDescription {
        margin-bottom: 5px;
        font-size: 16px;
      }

      b {
        font-size: 14px;
      }
    }

    .removeBtn {
      opacity: 0.5;
      transition: all 0.1s ease-in-out;
      cursor: pointer;

      &:hover {
        opacity: 1;
      }
    }
  }

  .cartTotalBlock {
    li {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;

      div {
        flex: 1;
        height: 1px;
        border-bottom: 1px dashed #dfdfdf;
        position: relative;
        top: -4px;
        margin: 0 10px;
      }

    }

    .greenButton {
      position: relative;

      &:hover {
        img {
          transform: translateX(7px);
        }
      }

      img {
        position: absolute;
        right: 70px;
        top: 22px;
        transition: all .2s ease-in-out;

      }
    }
  }


}

.greenButton {
  display: block;
  margin: 0 auto;
  width: 100%;
  width: 325px;
  height: 55px;
  border-radius: 18px;
  background: #9dd558;
  border: none;
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  transition: all .2s ease-in-out;
  cursor: pointer;

  &:hover {
    background: lighten(#9dd558, 10%);
  }

  &:active {
    background: darken(#9dd558, 5%);
  }
}